export function format(first: string | undefined, middle: string | undefined, last: string | undefined): string {
  return (first || '') + (middle ? ` ${middle}` : '') + (last ? ` ${last}` : '');
}

/**
 * Gets present value
 * @param evento KeyboardEvent
 * @returns string
 */
export function getPresentValue(evento: KeyboardEvent): string {
  const input = evento.target as HTMLInputElement;
  const valor = input.value;
  const posicionCursor = input.selectionStart;
  const valorActualizado =
    valor.substring(0, posicionCursor as number) +
    evento.key +
    valor.substring(posicionCursor as number);

  return valorActualizado;
}

/**
 * Removes double spaces
 * @param input string
 * @returns string
 */
export function removeDoubleSpaces(value: string) {
  return value.replace(/\s{2,}/g, ' ');
}

export function getValueByPath<T>(data: T, parts: Array<string>): string {
  if (parts.length === 1) {
    return data[parts[0]];
  }

  const key = parts[0];
  const nextObj = data[key];

  if (nextObj) {
    return getValueByPath(nextObj, parts.slice(1));
  }

  return '';
}